import $ from "jquery";

export const ajaxRequestJSON = (url, type, data, success) => {
    $.ajax({
        url: url,
        type: type,
        dataType: "json",
        data: data,
        headers: {"so_token":localStorage.so_token},
        success: success
    })
}


