import React, {Component} from 'react'
import {Modal} from "react-responsive-modal";
import PostAdd from "../component/forum/post/post-add";

import PropTypes from "prop-types";
import $ from "jquery";
import {ajaxRequestJSON} from "../js/so_common";
import './forum.css';


export default class Forum extends Component {
    static propTypes = {
        loginStatus: PropTypes.bool.isRequired
    }

    state = {
        open: false
    };

    onOpenModal = () => {
        this.setState({open: true});
    };

    onCloseModal = () => {
        this.setState({open: false});
    };

    closeUponSubmission = () => {
        this.setState({open: false});
    }


    get_posts = () => {
        ajaxRequestJSON("/forumPostQueryServlet", "POST", $(this).serialize(), function (data) {
            if (data.flag) {
                let list = data.data;
                for (let forumPosts of Object.keys(list)) {

                    $('#ajax_post').prepend(
                        "<div class='row post_block' style='margin-top: 15px;'>" +
                        "                <div class='col-sm-2'>" +
                        "                    <div class='profile-holder'>" +
                        "                        <img src='img/avatar_2x.png' class=\'profile-picture fp-" + list[forumPosts].pid + "\' alt='avatar'/>" +
                        "                    </div>" +
                        "                    <div class=\'User-name fp-" + list[forumPosts].pid + "\'>" +
                        "                    </div>" +
                        "                </div>" +
                        "                <div class='col-sm-10'>" +
                        "                        <a href=" + list[forumPosts].link + " class='title'>" +
                        list[forumPosts].title +
                        "                        </a>" +
                        "                    <div class='postTime'>" +
                        list[forumPosts].parseReleaseDate + " <span>&nbsp;</span>" +
                        "                    </div>" +
                        "                </div>" +
                        "                <input type='text' class='_sid' disabled hidden>" +
                        "                <input type='text' class='_uid' name='uid' disabled hidden>" +
                        "</div>")

                    ajaxRequestJSON("/postProfileQueryServlet", "POST",{"post_uid":list[forumPosts].uid}, function(data) {

                        $("div[class=\'User-name fp-" + list[forumPosts].pid + "\']").html(data.data.name);

                        $.post("/avatarQueryServlet", {"hasAvatar":"false", "avatarUid":list[forumPosts].uid},function(data){
                            $("img[class=\'profile-picture fp-" + list[forumPosts].pid + "\']").attr("src",data);
                        })
                    })

                    $("._uid").attr("value", list[forumPosts].uid);
                    $("._sid").attr("value", list[forumPosts].sid);
                }
            }
        })
    }

    componentDidMount() {
        this.get_posts();
    }


    render() {
        const {loginStatus} = this.props
        const {open} = this.state;
        return (
            <div>
                <div className='head'>
                    <span>Welcome to Scratch One Forum</span>
                    <button onClick={this.onOpenModal} className='post-bottom'>Post here</button>
                    <Modal open={open} onClose={this.onCloseModal} classNames='pop-up'>
                        <PostAdd get_posts={this.get_posts} closeUponSubmission={this.closeUponSubmission} loginStatus={loginStatus}/>
                    </Modal>
                </div>
                <div className='post_list' id="ajax_post"></div>
            </div>
        )
    }
}


