import React, {Component} from 'react'
import "react-responsive-modal/styles.css";
import $ from 'jquery';

import './app.css'
import Forum from './forum'
import logo from '../img/logo.png'
import {ajaxRequestJSON} from '../js/so_common'
import {load_navbar_content_user,load_navbar_content_lns,load_navbar_content,user_logout} from '../js/navbar_content'

export default class App extends Component {

    state = {
        post_uid : '',
        loginStatus: false,
    }


    load_login_status = () => {
        ajaxRequestJSON("/loginStatusServlet","POST",$(this).serialize(), (data) =>  {
            this.setState({
                loginStatus: data.flag
            }, function () {
                if(this.state.loginStatus) {
                    load_navbar_content_user(data.data.name);
                    $('#uid_cache').val(data.data.uid);
                    $('#log_out').on("click",user_logout);
                } else {
                    load_navbar_content_lns();
                }
            })
        })
    }


    componentDidMount() {
        load_navbar_content();
        this.load_login_status();
    }


    render() {
        const {loginStatus} = this.state
        return (
            <div className="container-fluid">
                <section className="flex">
                    <div className="content">
                        <div className="top">
                            <img src={logo} alt={'logo'} id="logo"/>
                        </div>
                        <nav className="navbar navbar-expand-lg navbar-dark "
                             style={{clear: "both", backgroundColor:"black",width: "100%"}}>
                            <li className="nav-item" id="now"><a href="#" className="nav-link" id="navbar_forum"
                                                                 style={{color: "white"}}>Forum</a></li>
                            <button type="button" className="navbar-toggler" data-toggle="collapse"
                                    data-target="#navbarCollapse">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarCollapse">
                                <ul className="navbar-nav mr-auto">
                                    <li className="nav-item">
                                        <a className="nav-link" href="/home.html" style={{color: "white"}}>Home</a>
                                    </li>
                                    <li className="nav-item">
                                        <div className="nav-item dropdown">
                                            <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown"
                                               style={{color: "white"}}>School</a>
                                            <div className="dropdown-menu" id="navbar_schools"></div>
                                        </div>
                                    </li>
                                    <li className="nav-item">
                                        <div className="nav-item dropdown">
                                            <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown"
                                               style={{color: "white"}}>Majors</a>
                                            <div className="dropdown-menu" id="navbar_majors"></div>
                                        </div>
                                    </li>
                                    <li className="nav-item">
                                        <div className="nav-item dropdown">
                                            <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown"
                                               style={{color: "white"}}>Overseas
                                                Opportunities</a>
                                            <div className="dropdown-menu" id="navbar_overseas_opportunities"></div>
                                        </div>
                                    </li>

                                    <li className="nav-item">
                                        <a className="nav-item nav-link " style={{color: "white"}}
                                           id="navbar_other_resources">Other resources</a>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                        <Forum loginStatus={loginStatus} />
                    </div>
                    <div className="bottom">Copyright @ 2020 Scratch One</div>
                </section>
            </div>

        )
    }
}





