import React, {Component} from 'react'
import PropTypes from 'prop-types'
import $ from "jquery";
import {ajaxRequestJSON} from '../../../js/so_common'

export default class PostAdd extends Component {
    static propTypes = {
        get_posts: PropTypes.func.isRequired,
        closeUponSubmission: PropTypes.func.isRequired,
        loginStatus: PropTypes.bool.isRequired
    }

    state = {
        post_title: '',
        post_content: '',

    }


    handleSubmit = () => {
        if (this.props.loginStatus) {
            const {closeUponSubmission,get_posts} = this.props

                this.setState(
                    {
                        post_title: '',
                        post_content: '',
                    }
                )


                closeUponSubmission();

                ajaxRequestJSON("/addPostServlet", "POST", {
                        "post_uid": $('#uid_cache').val(),
                        "post_releaseDate": new Date().toUTCString(),
                        "post_title": this.state.post_title,
                        "post_content": this.state.post_content
                    }, (data) => {
                        if (data.flag) {
                            $(".post_block").remove();
                            get_posts();
                        } else {
                            window.alert(data.errorMsg);
                        }
                    }
                )
        } else {
            window.alert('Sorry. You have to log in to post.')
            this.props.closeUponSubmission();
        }

    }

    handleContentChange = (event) => {
        const post_content = event.target.value
        this.setState({post_content})
    }
    handleTitleChange = (event) => {
        const post_title = event.target.value
        this.setState({post_title})
    }

    render() {
        const {post_content, post_title} = this.state
        return (
            <div>
                <form className="form-horizontal">
                    <div className="form-group">
                        <label>Your title</label>
                        <textarea className="form-control" placeholder="Tile" value={post_title}
                                  onChange={this.handleTitleChange}/>
                        <label>Your content</label>
                        <textarea className="form-control" placeholder="Content" value={post_content}
                                  onChange={this.handleContentChange}/>
                    </div>
                    <div className="form-group">
                        <div className="col-sm-offset-2 col-sm-10">
                            <button type="button" className="btn btn-default pull-right"
                                    onClick={this.handleSubmit}>Submit
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}


